<template>
  <div class="main">
    <h2 class="title1">  
    </h2>
    <div class="solid">
      <div class="dash">
        <h2 class="title2"></h2>
        <p>点击“开始答题”后，系统将从题库中随机 抽选五道题。题目主要围绕“双创”等主题。 用户答对其中四道题目，即可参与抽奖。
        </p>
        <h2 class="title3"></h2>
        <p>微信红包、腾讯视频会员卡
          <br>平安出行红包
        </p>
      </div>
    </div>
    <button><span>开始答题</span></button>
    <div class="decorate">
      <img src="../img/index_15.png" alt="">
    </div>
  </div>
</template>
<script>
export default {
  name: 'hello',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
.main {
  height: 9rem;
}

h2 {
  height: 0.86rem;
  width: 100%;
  img {
    width: 5.9rem;
    height: 0.86rem;
  }
}

.solid {
  width: 5.7rem;
  height: 4.8rem;
  box-sizing: border-box;
  padding: 0.1rem;
  border: 0.05rem solid #975d14;
  margin: 0.65rem auto;
  border-radius: 0.3rem;
  .dash {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border: 0.05rem dashed #975d14;
    padding: 0 0.1rem;
    border-radius: 0.2rem;
    h2 {
      font-size: 0.34rem;
      color: #144a62;
      text-shadow: 0 0 0.1rem #fff;
      font-weight: bold;
    }
    p {
      font-size: 0.26rem;
      color: #0e394b;
      line-height: 0.48rem;
    }
  }
}

button {
  background: url('../img/index_11.png') no-repeat center;
  font-size: 0.48rem;
  color: #fff;
  width: 3.6rem;
  height: 0.88rem;

  background-size: cover;
  border: 0;
  margin-top: 0.5rem;
  span{
    position: relative;
    top:-0.03rem; 
    line-height: 0.7rem;
  }
}

.decorate {
  width: 100%;
  height: 0.01rem;
  position: relative;
  img {
    position: absolute;
    right: 0.3rem;
    bottom: 0.1rem;
    width: 1.9rem;
    height: 3.2rem;
  }
}

h2 {
  &.title1 {
    background: url('../img/title_03.png') no-repeat center 0;
    background-size: 5.99rem 1.96rem;
    width: 5.99rem;
    height: 0.99rem;
    margin: 0 auto;
  }
  &.title2 {
    background: url('../img/title_03.png') no-repeat 0 -1.02rem;
    background-size: 5.99rem 1.96rem;
    width: 1.63rem;
    height: 0.44rem;
    margin: 0.25rem auto 0.15rem;
  }
  &.title3 {
    background: url('../img/title_03.png') no-repeat 0 -1.50rem;
    background-size: 5.99rem 1.96rem;
    width: 1.63rem;
    height: 0.44rem;
    margin: 0.25rem auto 0.15rem;
  }
}

</style>



